* {
  color: inherit;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  font-family: DM Sans, sans-serif;
}

ul {
  list-style: none;
}

img {
  display: block;
}

.container {
  max-width: 480px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    min-width: 1200px;
    max-width: 1280px;
  }
}

.button__buy {
  display: none;
}

@media screen and (min-width: 768px) {
  .button__buy {
    width: 160px;
    height: 44px;
    color: #d41443;
    cursor: pointer;
    z-index: 1;
    background-color: #fff;
    border: none;
    border-radius: 22px;
    outline: none;
    justify-content: center;
    align-items: center;
    margin-right: 34px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.31;
    transition: transform .2s;
    display: flex;
    box-shadow: 0 4px 4px #00000040;
  }
}

.button__label {
  padding-left: 5px;
}

.button__buy:hover {
  background-color: #ecebeb;
  transform: scale(1.05);
}

.button__buy:hover > .button__arrow {
  border-color: text-hover-color;
  transform: rotate(-45deg)translate(0);
}

.button__buy:hover > .button__arrow:before {
  opacity: 1;
  width: 7px;
}

@media screen and (min-width: 1280px) {
  .button__buy:hover > .button__arrow:before {
    width: 9px;
  }
}

.button__red {
  width: 62.4px;
  height: 24px;
  filter: drop-shadow(0 4px 4px #00000040);
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 20px;
  align-items: center;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  transition: transform .2s;
}

@media screen and (min-width: 1280px) {
  .button__red {
    width: 104px;
    height: 40px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.button__red:hover, .button__red:focus {
  background-color: #c7133e;
  outline: none;
  transform: scale(1.05);
}

.button__white {
  width: 82.8px;
  height: 24px;
  filter: drop-shadow(0 4px 4px #00000040);
  color: #d41443;
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  align-items: center;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  transition: transform .2s;
}

@media screen and (min-width: 1280px) {
  .button__white {
    width: 138px;
    height: 40px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.button__white--mobile-menu {
  width: 209px;
  height: 44px;
  text-align: center;
  padding: 12px 48.64px;
  font-size: 16px;
  font-weight: 700;
}

.button__white:hover, .button__white:focus {
  background-color: #ecebeb;
  outline: none;
  transform: scale(1.05);
}

.button__slider {
  display: none;
}

@media screen and (min-width: 768px) {
  .button__slider {
    width: 18px;
    height: 18px;
    cursor: pointer;
    border: none;
    border-radius: 50%;
    align-items: center;
    margin-left: 154px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .button__slider {
    width: 26px;
    height: 26px;
  }
}

.button__arrow {
  width: 6px;
  height: 6px;
  border-bottom: 1px solid #d41443;
  border-right: 1px solid #d41443;
  margin: 0 6px;
  transition: all .3s;
  position: relative;
  transform: rotate(-45deg);
}

@media screen and (min-width: 1280px) {
  .button__arrow {
    width: 8px;
    height: 8px;
    border-bottom: 1px solid #d41443;
    border-right: 1px solid #d41443;
    margin: 0 8px;
  }
}

.button__arrow:before {
  width: 3px;
  transform-origin: 100% 100%;
  height: 1px;
  opacity: 0;
  content: "";
  background-color: #d41443;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -.5px;
  right: 0;
  transform: rotate(45deg);
}

.button__slider:hover > .button__arrow {
  border-color: text-hover-color;
  transform: rotate(-45deg)translate(0);
}

.button__slider:hover > .button__arrow:before {
  opacity: 1;
  width: 7px;
}

@media screen and (min-width: 1280px) {
  .button__slider:hover > .button__arrow:before {
    width: 9px;
  }
}

