.mobile-menu__wrapper {
  height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 19px;
  display: flex;
  position: relative;
}

.menu-toggle {
  cursor: pointer;
  background-color: #ffb8ca;
  border: none;
  outline: none;
  position: absolute;
  top: 25px;
  right: 20px;
}

.mobile-menu {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  text-align: center;
  background-color: #e17992;
  padding-top: 60px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu__list {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

.mobile-menu__list:last-child {
  border-bottom: 1px solid #e18298;
}

.mobile-menu__link {
  height: 42px;
  text-align: left;
  color: #fff;
  border-top: 1px solid #e18298;
  align-items: center;
  padding-left: 21px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.285;
  text-decoration: none;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  display: flex;
}

.mobile-menu__link:active, .mobile-menu__link:focus, .mobile-menu__link:hover {
  color: #d41443;
}

.button__arrow-icon {
  stroke: #d41443;
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.js-close-menu {
  width: 14px;
  height: 14px;
  stroke: #fff;
  background-color: #e17992;
  position: absolute;
  top: 23px;
  right: 21px;
}

.js-close-menu:hover, .js-close-menu:focus {
  stroke: #d41443;
}

@media screen and (min-width: 767px) {
  .menu-toggle {
    display: none;
  }
}

